 white;\r\n$primary-color: #A6AA9C;\r\n$secondary-color: #E6E7E5;\r\n$font-black: black;\r\n$danger-color: red;\r\n*{\r\n    padding: 0;\r\n    margin: 0;\r\n    box-sizing: border-box;\r\n    font-family: Arial, Helvetica, sans-serif;\r\n    P{\r\n        word-spacing: 1px;\r\n        letter-spacing: 1px;\r\n        line-height: 24px;\r\n    }\r\n    &:focus{\r\n        border: 4px solid skyblue;\r\n\r\n    }\r\n}\r\nbody{\r\n    scroll-behavior: smooth;\r\n    display: inline;\r\n    overflow-x: hidden;\r\n\r\n}\r\n.skip{\r\n    min-width: fit-content;\r\n    height: 100%;\r\n    z-index: 10;\r\n    position: absolute;\r\n    top: 100px;\r\n    left: 20px;\r\n    background-color: $font-white;\r\n    border-radius: 10px;\r\n    padding: 10px;\r\n    border: 2px $font-black solid;\r\n    border: none;\r\n    opacity: 0;\r\n    a{\r\n        padding: 20px;\r\n        width: fit-content;\r\n        height: 44px;\r\n        font-size: 20px;\r\n        color: $font-black;\r\n        text-decoration: none;\r\n        font-weight: bolder;\r\n        border: none;\r\n    }\r\n\r\n    &:focus-within{\r\n        width: fit-content;\r\n        height: fit-content;\r\n        font-size: 20px;\r\n        opacity: 1;\r\n        border: 2px $secondary-color solid;\r\n    }\r\n}\r\nheader{\r\n    z-index: 1000;\r\n    position: sticky;\r\n    top: 0;\r\n    padding: 10px 50px;\r\n    background-color: $primary-color;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    width: 100%;\r\n    box-shadow: 0 4px 8px 2px rgba(0,0,0,0.5);\r\n    img{\r\n        flex: 1;\r\n        max-width: 50px;\r\n        height: 50px;\r\n        max-height: 50px;\r\n    }\r\n    #btnHamburger {\r\n        width: 60px;\r\n        height: 60px;\r\n        background-color: transparent;\r\n        border: none;\r\n        -webkit-transform: translate(0, 0);\r\n        transform: translate(0, 0);\r\n        img{\r\n            min-width: 60px;\r\n            min-height: 60px;\r\n        }\r\n    }\r\n    nav{\r\n        z-index: 100;\r\n        flex: auto;\r\n        ul{\r\n            display: flex;\r\n            justify-content: right;\r\n            align-items: center;\r\n            gap: 3rem;\r\n        overflow-x: hidden;\r\n\r\n            li{\r\n                min-width: 50px;\r\n                min-height: 50px;\r\n                list-style-type: none;\r\n                display: flex;\r\n                align-items: center;\r\n                justify-content: center;\r\n                a{      \r\n                    text-decoration: none;\r\n                    color: $font-white;\r\n                    font-weight: bolder;\r\n                    min-width: 60px;\r\n                    min-height: 60px;\r\n                    display: flex;\r\n                    align-items: center;\r\n                    justify-content: center;\r\n                    font-size: 20px;\r\n\r\n                    &:hover{\r\n                        color: $font-color;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\nmain{\r\n    width: 100%;\r\n    background-color: $backgrund-color;\r\n    scroll-behavior: smooth;\r\n\r\n    .slider{\r\n        z-index: -100;\r\n        width: 100%;\r\n        max-width: 100%;\r\n        height: 550px;\r\n        overflow: hidden;\r\n        .slides{\r\n            z-index: -100;\r\n            width: 500%;\r\n            height: 550px;\r\n            display: flex;\r\n            input{\r\n                width: 90px;\r\n                height: 90px;\r\n                display: none;\r\n            }\r\n           \r\n            #radio1:checked ~ .first{\r\n                margin-left: 0;\r\n            }\r\n            #radio2:checked ~ .first{\r\n                margin-left: -20%;\r\n            }\r\n            #radio3:checked ~ .first{\r\n                margin-left: -40%;\r\n            }\r\n            #radio4:checked ~ .first{\r\n    